import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Joi from '@hapi/joi';
import Input from '../common/Input/Input';
import Button from '../common/Button/Button';
import FormCard from '../common/FormCard/FormCard';
import styles from './Login.module.css';

class Login extends Component {
  state = {
    email: '',
    password: '',
    keep: true,
    errors: {}
  };

  schema = {
    email: Joi.string()
      .email()
      .required()
      .label('Email'),
    password: Joi.string()
      .required()
      .label('Password'),
    keepMe: Joi.boolean()
    .required()
  };

  handleChange = event => {
    if (event.target.type === 'checkbox') {
      this.setState({ [event.target.name]: !this.state.keep });
      return;
    }
    this.setState({ [event.target.name]: event.target.value });
  };

  handleSubmit = event => {
    event.preventDefault();
    const user = {
      email: this.state.email,
      password: this.state.password,
      keepMe: this.state.keep
    };
    const { error } = Joi.validate(user, this.schema);
    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
    } else {
      this.props.UserLogin(user, this.state.keep).then(receivedUser => {
        localStorage.setItem('user', JSON.stringify(receivedUser));
      });
    }
  };

  componentDidUpdate(prevProps) {
    if (prevProps.LoginError !== this.props.LoginError) {
      this.setState({ errors: this.props.LoginError });
    }
  }

  render() {
    return (
      <>
        <FormCard>
          {this.state.errors.loginError ? <h2>{this.state.errors.loginError}</h2> : null}
          <form onSubmit={event => this.handleSubmit(event)}>
            <Input
              type="text"
              name="email"
              placeholder="E-Mail"
              value={this.state.email}
              error={this.state.errors.email}
              onChange={event => this.handleChange(event)}
            />

            <Input
              type="password"
              name="password"
              placeholder="Password"
              value={this.state.password}
              error={this.state.errors.password}
              onChange={event => this.handleChange(event)}
            />

            <div className={styles.meta}>
              <div className={styles.checkBoxContainer}>
                <label className={styles.keepMeLoggedIn}>
                  Keep me logged in
                  <input type="checkbox" className="checkbox" name="keep" checked={this.state.keep} onChange={event => this.handleChange(event)} />
                  <span className={styles.checkmark}> </span>
                </label>
              </div>

              <span className={styles.recover}>
                <Link to="/recover-account">Forgot Password?</Link>
              </span>
            </div>

            <div className={styles.error}>
              {Object.keys(this.state.errors).map(error => (
                <span key={error}>{this.state.errors[error]}</span>
              ))}
            </div>

            <Button className="fancySubmit" type="submit">
              Sign In
            </Button>
          </form>
        </FormCard>
      </>
    );
  }
}

export default Login;
