import React, { Component } from 'react';
import Joi from '@hapi/joi';
import styles from './CreateForm.module.css';

class CreateForm extends Component {
  state = {
    title: '',
    errors: {},
    author_id: this.props.User.id
  };

  Schema = {
    title: Joi.string()
      .min(4)
      .required()
      .label('name')
      .max(200),
    author_id: Joi.number()
      .min(1)
      .required()
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { title, author_id } = this.state;
    let newOne = {
      title: title,
      author_id: author_id
    };
    const { error } = Joi.validate(newOne, this.Schema);
    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
    } else {
      if (this.props.name === 'technology') {
        this.props.createTechnology(newOne, this.props.User.token);
      } else if (this.props.name === 'project') {
        this.props.createProject(newOne, this.props.User.token);
      }
    }
    this.setState({
      title: ''
    });
    this.props.hide();
  };

  handleCancel = e => {
    e.preventDefault();
    this.setState({
      title: ''
    });
    this.props.hide();
  };

  render() {
    return (
      <div
        style={{
          transform: this.props.show ? 'translateY(1vh)' : 'translateY(-20vh)',
          opacity: this.props.show ? '1' : '0'
        }}
      >
        <form className={styles.form} onSubmit={e => this.handleSubmit(e)}>
          <div className={styles.inputBox}>
            <label className={styles.label} htmlFor="title">
              Title: 
            </label>
            <input className={styles.input} value={this.state.title} type="text" name="title" placeholder="Enter Name" onChange={this.handleChange} />
            {this.state.errors.name ? <p> {this.state.errors.name} </p> : null}
          </div>
          <div className={styles.btnBox}>
            <button className={styles.submit} type="submit">
              Submit
            </button>
            <button className={styles.submit} type="reset" onClick={e => this.handleCancel(e)}>
              Cancel
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default CreateForm;
