import React, { Component } from 'react';
import RegistrationFormView from '../../../../../containers/RegistrationFormView';
import styles from './ProfileItem.module.css';
import Button from '../../../../common/Button/Button';
import PermissionRuler from '../../../../../hoc/PermissionRuler';
import delete_icon from '../../../../../assets/images/delete_icon.png';

class ProfileItem extends Component {
  state = {
    show: false
  };

  showEditUserForm = () => {
    console.log(this.props.Profile.id)
    this.setState({ show: !this.state.show });
  };

  handleDeletProfile = id => {
    this.props.DeleteProfile(id);
    this.props.history.replace(`/home`);
  };

  handleUpload = e => {
    let formData = new FormData();
    let file = e.target.files[0];
    formData.append('file', file);
    formData.append('author_id', this.props.User.id);
    formData.append('user_id', this.props.Profile.id);
    // let ragaca = new objectToFormData({
    //   name: 'file',
    //   file: file
    // })
    this.props.UploadFile(formData);
    e.target.value = '';
  };

  handleDeleteFile = id => {
    this.props.DeleteFile(id);
  };

  handleDownload = path => {
    window.open(`http://laravel.local/${path}`, '_blank');
  };

  componentDidMount() {
    if (!this.props.Profile) {
      this.props.GetAllProfiles();
    }
    if (this.props.files) {
      this.props.GetFiles(this.props.match.params.id);
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.files.length !== this.props.files.length) {
      this.props.GetFiles(this.props.match.params.id);
    }
  }

  handleDeleteProfile = id => {
    this.props.DeleteProfile(id);
    this.props.history.replace('/home');
  };

  render() {
    console.log(this.props.Profile)
    return (
      <div>
        <PermissionRuler roles={['admin', 'manager']}>
          <Button className="filter" onClick={this.showEditUserForm}>
            Edit Profile
          </Button>
          <Button className="delete" onClick={() => this.handleDeleteProfile(this.props.Profile.id)}>
            Delete Profile
          </Button>
          {this.state.show ? <RegistrationFormView form="edit" showHide={this.showEditUserForm} currentProfile={this.props.Profile} /> : null}
        </PermissionRuler>

        <div className={styles.itemContainer}>
          <div className={styles.itemContainerRow}>
            {this.props.Profile ? (
              <>
                <div className={styles.itemBox}>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}>Name</p> <p>{this.props.Profile.name}</p>
                  </div>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}>Profile</p> <p>{this.props.Profile.profile}</p>
                  </div>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}>Phone</p> <p>{this.props.Profile.phone}</p>
                  </div>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}>Comment</p>
                    <p>{this.props.Profile.comment}</p>
                  </div>
                </div>

                <div className={styles.itemBox}>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}> Portfolio/Projects</p>
                    {this.props.Profile.projects.map(project => {
                      return <span key={project.id}> {project.title}</span>;
                    })}
                  </div>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}> Technologies</p>
                    {this.props.Profile.technologies.map(technology => {
                      return <span key={technology.id}> {technology.title}</span>;
                    })}
                  </div>
                  <div className={styles.itemBoxDiv}>
                    <p className={styles.itemTitle}>Position</p> <span>{this.props.Profile.position}</span>
                  </div>
                </div>
              </>
            ) : null}
          </div>
        </div>
        <div className={styles.filesBox}>
          <input type="file" name="fileItem" onChange={this.handleUpload} className={styles.fileInput} />
          <div className={styles.filesList}>
            {this.props.files
              ? (this.props.files.map(item => {
                  return (
                    <div key={item.id} className={styles.linkBox}>
                      <a href={`http://139.59.131.157/${item.path}`} target="_blank" rel="noopener noreferrer" className={styles.fileLink}>
                        {item.title}
                      </a>
                      <button className={styles.deleteBtn} onClick={() => this.handleDeleteFile(item.id)}>
                        <img className={styles.deleteBtn_img} src={delete_icon} alt="delete_icon" />
                      </button>
                      <br />
                    </div>
                  );
                }))
              : null}
          </div>
        </div>
      </div>
    );
  }
}

export default ProfileItem;
