import {connect} from 'react-redux';
import CreateForm from '../components/Home/CreateForm/CreateForm';
import {CreateTechnology} from '../actions/TechnologyActions';


const mapStateToProps = state => {
    return {
        AllTechnologies: state.TechnologyReducer.AllTechnologies,
        User: state.UserReducer.User
    }
}

const mapDispatchToProps = dispatch => {
    return{
        createTechnology: (technology,token) => {
            dispatch(CreateTechnology(technology,token))
        }
    }
}

const CreateTechnologyView = connect(mapStateToProps,mapDispatchToProps)(CreateForm)

export default CreateTechnologyView;