import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styles from './Technologies.module.css';
import CreateTechnologyView from '../../../../containers/CreateTechnologyView';
import delete_icon from '../../../../assets/images/delete_icon.png';
import PermissionRuler from '../../../../hoc/PermissionRuler';

import plusIcon from '../../../../assets/images/icons8-plus-64.png';

class Technologies extends Component {
  state = {
    show: false,
    technology: {}
  };
  handleShowHide = () => {
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };

  componentDidMount() {
    this.props.GetAllTechnologies();
  }

  handleDelete = id => {
    this.props.DeleteTechnology(id);
  };

  // handleEditTechnology = technology => {
  //   this.props.EditTechnology(technology);
  //   this.setState({
  //     show: !this.state.show
  //   });
  // };

  render() {
    return (
      <>
        <PermissionRuler roles={['admin', 'manager']}>
          <button onClick={this.handleShowHide} className={styles.technologytBTN}>
            <img src={plusIcon} alt="add technology" className={styles.plusIcon} /> Create Technology
          </button>
          <CreateTechnologyView hide={this.handleShowHide} name="technology" show={this.state.show} />
        </PermissionRuler>
        <div className={styles.technologies_item}>
          {this.props.AllTechnologies
            ? this.props.AllTechnologies.map(technology => {
                return (
                  <div key={technology.id} className={styles.item_conteiner}>
                    <Link to={`technologies/${technology.id}`} className={styles.technologies_links}>
                      <div> {technology.title} </div>
                    </Link>
                    <button onClick={() => this.handleDelete(technology.id)} className={styles.deleteBtn}>
                      <img src={delete_icon} alt="delete_icon" className={styles.deleteBtn_img} />
                    </button>
                  </div>
                );
              })
            : null}
        </div>
      </>
    );
  }
}

export default Technologies;
