import React, { Component } from 'react';
import styles from './ProfileList.module.css';
import Table from '../../../common/Table/Table';
import Button from '../../../common/Button/Button';
import Filter from '../../../../containers/FilterFormView';
import RegistrationFormView from '../../../../containers/RegistrationFormView';
import PermissionRuler from '../../../../hoc/PermissionRuler';

import filterIcon from '../../../../assets/images/icons8-filter.png';
import createIcon from '../../../../assets/images/icons8-create.png';

class ProfileList extends Component {
  state = {
    // showFilter: false
    // showRegister: false
  };
  GetAllProfiles = () => {
    this.props.GetAllProfiles();
  };
  componentDidMount() {
    this.props.GetAllProfiles();
  }

  showFilter = () => {
    this.props.toggleFilter();
    this.props.toggleRegister(false);
  };
  showRegister = () => {
    this.props.toggleFilter(false);
    this.props.toggleRegister();
  };
  render() {
    let data = [...this.props.AllProfiles];
    return (
      <>
        {this.props.showFilter === true ? <Filter form={'filter'} /> : null}
        {this.props.showRegister === true ? <RegistrationFormView form={'register'} /> : null}
        <div className={styles.searchBox} />
        <div className={styles.contFlexBTN}>
          <div className={styles.flexBtns}>
            <Button className="filter" onClick={() => this.showFilter()}>
              <img src={filterIcon} alt="filter" className={styles.FilterIcon} /> Filter
            </Button>
            <PermissionRuler roles={['admin', 'manager']}>
              <Button className="filter" onClick={() => this.showRegister()}>
                <img src={createIcon} alt="create profile" className={styles.createPIcon}/> Create Profile
              </Button>
            </PermissionRuler>
            <Button className="filter" onClick={() => this.GetAllProfiles()}>
              Clear Filter
            </Button>
          </div>
        </div>
        <Table data={data} />
      </>
    );
  }
}
export default ProfileList;
