import React, { Component } from 'react';
import ReactTable from 'react-table';
import { withRouter } from 'react-router-dom';

import 'react-table/react-table.css';
import './Table.css';
import ExportExcel from '../../Home/Export/ExportExcel';
import ExportPdf from '../../Home/Export/ExportPdf';

import belowIcon from '../../../assets/images/icons8-below-96.png';

class Table extends Component {
  state = {
    reorder: [],
    trigger: 0,
    dragged: null,
    show: false
  };

  handleShowHide = e => {
    e.preventDefault();
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };
  handleDragging() {
    const headers = Array.prototype.slice.call(document.querySelectorAll('.table-header'));
    headers.forEach((header, i) => {
      header.setAttribute('draggable', true);
      header.ondragstart = e => {
        e.stopPropagation();
        this.setState({ dragged: i });
      };
      header.ondrag = e => e.stopPropagation;
      header.ondragend = e => {
        e.stopPropagation();
        setTimeout(() => {
          this.setState({ dragged: null });
        }, 1000);
      };

      header.ondragover = e => e.preventDefault();

      header.ondrop = e => {
        e.preventDefault();
        this.setState({ reorder: [...this.state.reorder, { a: i, b: this.state.dragged }] });
        this.setState({ trigger: Math.random() });
        localStorage.setItem('order', JSON.stringify(this.state.reorder));
      };
    });
  }

  componentDidMount() {
    this.handleDragging();
    if (!localStorage.order) return;
    const order = JSON.parse(localStorage.order);
    this.setState({ reorder: order });
  }
  componentDidUpdate() {
    this.handleDragging();
  }

  getTrProps = (state, rowInfo, instance) => {
    if (rowInfo) {
      return {
        onClick: () => {
          let id = rowInfo.original.id;
          if (id) {
            return this.props.history.push(`/home/${id}`);
          }
        },
        style: {
          background:
            rowInfo.row.status === 'black_list'
              ? '#ffb3b1'
              : 'white' && (rowInfo.row.status !== 'black_list' && rowInfo.row.status === 'shortlisted')
              ? '#9ac48d'
              : 'white'
        }
      };
    }
    return {};
  };

  columns = [
    {
      Header: 'Projects',
      accessor: 'projects',
      width: 250
    },
    {
      Header: 'Profile',
      accessor: 'name',
      width: 150
    },
    {
      Header: 'Position',
      accessor: 'position',
      width: 200
    },
    {
      Header: 'Phone',
      accessor: 'phone',
      width: 130
    },
    {
      Header: 'LinkedIn',
      accessor: 'profile',
      width: 150
    },
    {
      Header: 'Portfolio',
      accessor: 'portfolio'
    },
    {
      Header: 'Comment',
      accessor: 'comment',
      width: 350
    },
    {
      Header: 'Technologies',
      accessor: 'technologies',
      width: 250
    },
    {
      Header: 'English',
      accessor: 'english'
    },
    {
      Header: 'Expected Salary',
      accessor: 'salary',
      width: 200
    },
    {
      Header: 'Source',
      accessor: 'source'
    },
    {
      Header: 'Status',
      accessor: 'status'
    },
    {
      Header: 'Created',
      accessor: 'created_at'
    },
    {
      Header: 'Updated',
      accessor: 'updated_at'
    }
  ];

  render() {
    const { data } = this.props;
    const cols = this.columns.map(col => ({
      ...col,
      Header: (
        <div key={col.Header} className="table-header">
          {col.Header}
        </div>
      )
    }));

    if (data) {
      var dataToApply = data.map(item => {
        let str1 = '';
        let str2 = '';
        if (item.technologies) {
          item.technologies.forEach(i => {
            str1 += `#${i.title} `;
          });
        }
        if (item.projects) {
          item.projects.forEach(i => {
            str2 += `#${i.title} `;
          });
        }
        return {
          ...item,
          technologies: str1,
          projects: str2
        };
      });
    }

    this.state.reorder.forEach(o => cols.splice(o.a, 0, cols.splice(o.b, 1)[0]));

    return (
      <div>
        <ReactTable
          {...this.props}
          data={dataToApply}
          columns={cols}
          defaultPageSize={100}
          sortable={false}
          ref={r => (this.currentPage = r)}
          getTrProps={this.getTrProps}
        >
          {(state, filtredData, instance) => {
            const dataToExport = state.pageRows.map(profile => {
              return profile._original;
            });
            return (
              <>
                <div className="exportBox">
                  <button onClick={this.handleShowHide} className="button">
                    <img src={belowIcon} alt="bellow" className="bellowIcon" /> Export
                  </button>
                  <div
                    className="exportBox__buttons"
                    style={{
                      transform: this.state.show ? 'translateY(0vh)' : 'translateY(-20vh)',
                      opacity: this.state.show ? '1' : '0'
                    }}
                  >
                    <ExportExcel profiles={dataToExport} />
                    <ExportPdf profiles={dataToExport} />
                  </div>
                </div>
                {filtredData()}
              </>
            );
          }}
        </ReactTable>
      </div>
    );
  }
}
export default withRouter(Table);
