import React, { Component } from 'react';

import CreateUserView from '../../../../containers/CreateUserView';
import Button from '../../../common/Button/Button';
import styles from './ManageUser.module.css';

import userIcon from '../../../../assets/images/personal.png';
import deleteIcon from '../../../../assets/images/trash-solid.svg';
import addUser from '../../../../assets/images/add-male-user.png';

import userEdit from '../../../../assets/images/userEdit.png';
import PermissionRuler from '../../../../hoc/PermissionRuler';;

class ManageUser extends Component {
  state = {
    show: false,
    user: {}
  };
  componentDidMount() {
    this.props.GetAllUsers();
  }

  handleShowHide = e => {
    e.preventDefault();
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };
  handleShowErrors = () => {
    return this.props.Error ? (
      <p>
        {this.props.Error.map((err, i) => {
          return <p key={i}>{err}</p>;
        })}{' '}
      </p>
    ) : null;
  };
  handleDeleteUser = user => {
    this.props.DeleteUser(user.id);
  };

  handleEditUser = user => {
    this.props.EditUser(user);
    this.setState({
      show: !this.state.show
    });
  };

  render() {
    return (
      <>
        <div className={styles.createBox}>
          <PermissionRuler roles={['admin']}>
            <Button className="createProfile" onClick={this.handleShowHide}>
              <img src={addUser} alt="add user" className={styles.addUserIcon} />  Create User
            </Button>
            <CreateUserView show={this.state.show} hide={this.handleShowHide} />
          </PermissionRuler>
        </div>
        <div className={styles.conteiner}>
          {this.props.AllUsers
            ? this.props.AllUsers.map((user, i) => {
                return (
                  <div key={i} className={styles.map_conteiner}>
                    <div className={styles.usersBox}>
                      <img src={userIcon} alt={user} className={styles.img} />
                      <p className={styles.username}>Name: {user.name} </p>
                      <p className={styles.email}> Role: {user.role} </p>
                      <PermissionRuler roles={['admin']}>
                        <button className={styles.edditBTN} onClick={() => this.handleEditUser(user)}>
                          <img src={userEdit} alt="user eddit" className={styles.userEdit} /> Edit
                        </button>
                        {user.role === 'admin' ?  null : <Button className={'delete_user'} onClick={() => this.handleDeleteUser(user)}>
                          <img src={deleteIcon} alt="delete user" className={styles.deleteUser} />
                          Delete User
                        </Button>}
                        
                      </PermissionRuler>
                    </div>
                  </div>
                );
              })
            : null}
        </div>
      </>
    );
  }
}

export default ManageUser;
