import React, { Component } from 'react';
import styles from './Sidebar.module.css';
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import menu from '../../../assets/images/menu.png';
import analytics from '../../../assets/images/analytics.png';
import calendar from '../../../assets/images/calendar.png';
import user from '../../../assets/images/user.png';
import project from '../../../assets/images/project.png';
import management from '../../../assets/images/management.png';
import technologies from '../../../assets/images/technologies.png';
import log_out from '../../../assets/images/log_out.png';

class Sidebar extends Component {
  state = {
    show: false
  };

  showHide = () => {
    this.setState({ show: !this.state.show }, function() {
    });
  };

  logOut =()=>{
    this.props.UserLogout();
    localStorage.clear();
  }

  render() {
    return (
      <>
        <div className={styles.blueCont}>
          <div className={styles.blueCont_item} onClick={this.showHide}>
            <img src={menu} alt="menu icon" />
          </div>
          <Link to="/home" className={styles.blueCont_item}>
            <img src={user} alt="user icon" />
          </Link>
          <Link to="/home/projects" className={styles.blueCont_item}>
            <img src={project} alt="project icon" />
          </Link>
          <Link to="/home/technologies" className={styles.blueCont_item}>
            <img src={technologies} alt="technologies icon" />
          </Link>
          <Link to="/home/analytics" className={styles.blueCont_item}>
            <img src={analytics} alt="analytics icon" />
          </Link>
          <Link to="/home/calendar" className={styles.blueCont_item}>
            <img src={calendar} alt="calendar icon" />
          </Link>
          <Link to="/home/manage-user" className={styles.blueCont_item}>
            <img src={management} alt="management icon" />
          </Link>
          <div className={styles.blueCont_item_version}>
            <div onClick={()=> this.logOut()} className={styles.logout_button}>
              <img src={log_out} alt="logout button" className={styles.logout_img} /> 
            </div>
            <h3 className={styles.version_text}>v1.0.0</h3>
          </div>
        </div>
        <br />
        <div className={this.state.show ? styles.sidebar : styles.sidebarHidden}>
          <div className={styles.list}>
            <NavLink exact to="/home" className={styles.list_item} activeClassName={styles.active}>
              Home
            </NavLink>
            <NavLink to="/home/projects" className={styles.list_item} activeClassName={styles.active}>
              Projects
            </NavLink>
            <NavLink to="/home/technologies" className={styles.list_item} activeClassName={styles.active}>
            Technologies 
            </NavLink>
            <NavLink to="/home/analytics" className={styles.list_item} activeClassName={styles.active}> 
              Analytics
            </NavLink>
            <NavLink to="/home/calendar" className={styles.list_item} activeClassName={styles.active}>
              Calendar
            </NavLink>
            <NavLink to="/home/manage-user" className={styles.list_item} activeClassName={styles.active}>
              Manage User
            </NavLink>
          </div>
        </div>
      </>
    );
  }
}

export default Sidebar;
