import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styles from './Projects.module.css';
import CreateProjectView from '../../../../containers/CreateProjectView';
import delete_icon from '../../../../assets/images/delete_icon.png';
import PermissionRuler from '../../../../hoc/PermissionRuler';

import plusIcon from '../../../../assets/images/icons8-plus-64.png';

class Projects extends Component {
  state = {
    show: false
  };

  handleShowHide = () => {
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };

  componentDidMount() {
    this.props.GetAllProjects();
  }

  handleDelete = id => {
    this.props.DeleteProject(id);
  };

  render() {
    return (
      <div className={styles.project_conteiner}>
        <PermissionRuler roles={['admin', 'manager']}>
          <button onClick={this.handleShowHide} className={styles.projectBTN}>
            <img src={plusIcon} alt="create" className={styles.plusIcon}/> Create Project
          </button>
          <CreateProjectView hide={this.handleShowHide} name="project" show={this.state.show} />
        </PermissionRuler>
        <div className={styles.project_item}>
          {this.props.AllProjects
            ? this.props.AllProjects.map((project, i) => {
                return (
                  <div key={i} className={styles.item_conteiner}>
                    <Link to={`project/${project.id}`} className={styles.project_links}>
                      {project.title}
                    </Link>
                    <button onClick={() => this.handleDelete(project.id)} className={styles.deleteBtn}>
                      <img src={delete_icon} alt="delete_icon" className={styles.deleteBtn_img} />
                    </button>
                  </div>
                );
              })
            : null}
        </div>
      </div>
    );
  }
}

export default Projects;
